<template>
  <div ref="containner">
    <Header @fixedMenu="fixedContainer"></Header>
    <router-view />
    <Footer />
  </div>
</template>
<script>
import {ref} from 'vue'
import Header from '@/components/header/index.vue';
import Footer from '@/components/footer/index.vue';

export default {
  components: {
    Footer,
    Header
  },
  setup(){
    let positionTop = ref(0)
    return {positionTop}
  },
  methods: {
    fixedContainer(e) {
      if(e){
        this.positionTop = window.pageYOffset
        this.$refs.containner.style = `position:fixed; top:-${this.positionTop}px`
      }
      else{
        this.$refs.containner.style = ``
        window.scrollTo(0, this.positionTop)
      }
    }
  },
}
</script>

<style lang="scss">
@import "assets/scss/main";
</style>
