<template>
    <div id="tab">
        <div class="tab">
            <div class="tab--btn">
                <button 
                v-for="(tab, index) in itemTab" 
                :key="index" 
                @click="showTab = index" 
                :class="{'active': showTab == index}"
                >{{tab.tabTitle}}</button>
            </div>
            <div class="tab--wrap">
                <ul class="tab--item" 
                v-for="(tab, index) in itemTab" 
                :key="index" 
                :class="{'tab--item__active': showTab===index}">
                    <li 
                    v-for="(news, index) in tab.tabContent" 
                    :key="index"><router-link to="#">{{news.news}}</router-link></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import {ref} from 'vue'
export default {
    props: {
        itemTab:{
            type: Array
        }
    },
    setup() {
        let showTab = ref(0);
        return {showTab}
    },
}
</script>
<style scoped lang="scss" src="@/assets/scss/common/tab/_tab.scss"></style>