<template>
    <div id="sliderbar">
        <button class="btn--next" @click="runSlide(100),deleteTime()"></button>
        <button class="btn--prev" @click="runSlide(-100),deleteTime()"></button>
        <div class="slidebar">
            <div class="slidebar--content" ref="silderBar">
                <div class="slidebar--item" v-for="(item, index) in slideBar" :key="index" ref="item__slidebar">
                    <router-link to="#">
                    <div class="slidebar--img">
                        <img :src="item.img" alt="">
                    </div>
                    <span>{{item.title}}</span>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {ref} from 'vue'
export default {
    setup() {
        let itemSlidebar = ref('')
        let quantityItem = ref(0)
        let translateX = ref(0)
        let numberItem = ref(4)
        let slidebarClick = ref(true)
        let autoClick = ref(true)
        let maxtsX = ref(0)
        
        let slideBar = ref([
            {
            title:" ng1 ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng2 ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng3 ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng4 ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" 08ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" 09ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:"10 ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },
            {
            title:" 11ng ch V Minh Tm gi chc Chnh Vn phng UBND tnh, ng ch  Tm Hin...",
            img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=162010&t=1658732067415"
            },           
        ])
        return{
            slideBar,itemSlidebar,quantityItem,translateX,autoClick,slidebarClick,numberItem,maxtsX
        }
    },
    methods:{
        loadSlide(){
            this.itemSlidebar = this.$refs.item__slidebar
            this.quantityItem = this.itemSlidebar.length
            this.maxtsX = this.quantityItem/this.numberItem * 100 - 100
        },
        setSlide(){
            for(let i = 0; i < this.itemSlidebar.length; i++){
                this.itemSlidebar[i].style = `left:calc(${i*25}% + 15px)` 
            }
        },
        sliderPlay:function(n){
            let maxSlide = Math.ceil(this.quantityItem/this.numberItem)*100
            if(n == maxSlide - 100){
                n = this.maxtsX
                this.translateX = this.maxtsX
            }
            if(n > maxSlide -100){
                n = this.maxtsX + 100
                for(let i = 0; i < this.numberItem; i++){
                    this.itemSlidebar[i].style = `left:calc(${(i+this.quantityItem)*25}% + 15px)` 
                }
                this.$refs.silderBar.addEventListener('transitionend', this.resetNext)
            }
            if(n > (-100) && n <0){
                n = 0
                this.translateX = 0
            }
            if(n <= (-100)){
                for(let i = this.quantityItem - this.numberItem; i < this.quantityItem; i++){
                    this.itemSlidebar[i].style = `left:calc(${(i-this.quantityItem)*25}% + 15px)`
                }
                this.$refs.silderBar.addEventListener('transitionend', this.resetPrev)
            }
            this.$refs.silderBar.style = `transform: translateX(${-n}%)`
        },
        runSlide:function(tsX){
            if(this.slidebarClick){
                this.translateX += tsX
                this.sliderPlay(this.translateX)
                this.slidebarClick = false
            }
            this.$refs.silderBar.addEventListener('transitionend',()=>{
                this.slidebarClick = true
            })
        },
        autoPlay(){
            this.timeRun = setInterval(() =>{
                if(this.quantityItem>4 && this.autoClick){
                    this.runSlide(100);
                }
            },4000)
        },
        deleteTime(){
            if(this.autoClick == true){
                this.settime = setTimeout(() => {
                    this.autoClick = true;
                },10000)
            }
            this.autoClick = false;
        },
        resetNext(){
            this.setSlide()
            this.loadSlide()
            this.translateX = 0
            this.$refs.silderBar.style = `transform: translateX(0);transition:none`
            this.$refs.silderBar.removeEventListener('transitionend', this.resetNext)
        },
        resetPrev(){
            this.setSlide()
            this.loadSlide()
            this.translateX = this.maxtsX
            this.$refs.silderBar.style = `transform: translateX(-${this.maxtsX}%);transition:none`
            this.$refs.silderBar.removeEventListener('transitionend', this.resetPrev)
        }
    },
    mounted() {
        this.loadSlide();
        this.setSlide();
        this.autoPlay();
    },
  beforeUnmount() {
    clearInterval(this.timeRun);
    clearTimeout(this.settime);
  }
}
</script>

<style scoped lang="scss" src="@/assets/scss/components/_sliderbar.scss"/>